/* ToDo Animation */
.enter {
  opacity: 0;
  transform: translateX(-100%);
  transition: all 250ms linear;
}

.enterActive{
  opacity: 1;
  transform: translateX(0);
}

.exit{
  opacity: 1;
  transform: translateX(0);
}

.exitActive{
  opacity: 0;
  transform: translateX(-100%);
  transition: all 250ms linear;
}