.main h4{
  position: fixed;
  right: 50px;
  top: 20px;

  color: white;
  background: rgb(255, 62, 62);
  padding: 20px;
  border-radius: 5px;
  width: auto;
  z-index: 10;
}

.enter {
  opacity: 0;
  transform: translateX(100%);
  transition: all 250ms linear;
}

.enterActive{
  opacity: 1;
  transform: translateX(0);
}

.exit{
  opacity: 1;
  transform: translateX(0);
}

.exitActive{
  opacity: 0;
  transform: translateX(100%);
  transition: all 250ms linear;
}